    </div>
</template>

<script>
import top from "@/components/index_mk/top";
import six from "@/components/index_mk/six_mg";
import titslot from "@/components/index_mk/tit_slot";
import business from "@/components/index_mk/business";
import info from "../../components/index_mk/information";
import signing from "../../components/index_mk/signing";
export default {
    data() {
        return {
            ruleForm: {
                name: "",
                pass: ""
            },
            checked: false,
            isLogin: true
        };
    },
    components: {
        top,
        six,
        titslot,
        business,
        info,
        signing,
    },
    created() {},
    mounted() {},
    methods: {}
};
</script>

<style scoped lang='scss'>
.page{
    padding-top: 0.7%;
    width: 1280px;
    min-height: 700px;
    margin: 0 auto;
}
</style>
