<template>
    <div class="module">
        <el-tabs v-model="activeName" @tab-click="handleClick">
            <el-tab-pane label="金融机构" name="1">
                <div class="img_wrap">
                    <img src="../../assets/img/jinrong1.png" alt>
                     <img src="../../assets/img/jinrong1.png" alt>
                </div>
            </el-tab-pane>
            <el-tab-pane label="医疗卫生" name="2">
                 <div class="img_wrap">
                </div>
            </el-tab-pane>
            <el-tab-pane label="公安交警" name="3">
                 <div class="img_wrap">
                </div>
            </el-tab-pane>
            <el-tab-pane label="学校教育" name="4">
