                </div>
            </el-tab-pane>
        </el-tabs>
    </div>
</template>

<script>
export default {
    data() {
        return {
            activeName: "1"
        };
    },
    components: {},
    created() {},
    mounted() {},
    methods: {
        handleClick(tab, event) {
            console.log(tab, event);
        }
    }
};
</script>

<style scoped lang='scss'>
.img_wrap {
    margin-top: 20px;
    margin-bottom: 20px;
    padding-bottom: 6rem;
    padding-left: 6.9rem;
    padding-right: 6.9rem;
    img {
        margin-left: 0;
        width: 180px;
        height: 150px;
        float: left;
            margin-right: 1.8rem;
    margin-bottom: 20px;
    }
}
.module {
    width: 94%;
    margin-bottom: 6rem;
    height: 280px;
    background: #fff;
}
</style>
<style scoped>
.module >>> .el-tabs__nav {
    margin-left: 210px;
    margin-top: 20px;
}
.module >>> .el-tabs__item {
    font-size: 16px;
    color: #222637;
    font-weight: 600;
    cursor: pointer;
}
</style>



