<template>
    <div class="module">
        <span class="line"></span>
        <span class="txt">{{title}}</span>
        <span class="line"></span>
    </div>
</template>

<script>
export default {
    data() {
        return {};
    },
    props: {
        title: {
            type: String,
            required: false
        }
    },
    components: {},
    created() {},
    mounted() {},
    methods: {}
};
</script>

<style scoped lang='scss'>
.line {
    display: inline-block;
    width: 30%;
    border-top: 2px solid #d6d6d6;
}
.txt {
    display: inline-block;
    margin-left: 3%;
    margin-right: 3%;
    color: #222637;
    font-size: 20px;
    font-weight: 600;
    vertical-align: -8%;
}
.module {
    margin-top: 1%;
    height: 60px;
    line-height: 60px;
    text-align: center;
}
</style>



