        <div class="wrap left">
            <div class="news-left-block"></div>
            <div class="news-title" style="border:none">
                <p>资讯中心</p>
                <div class="clearfix"></div>
            </div>

            <ul class="news-content">
                <li>
                    <p class="news-info">郑州地铁5号线国内首个实现三大运营商5G同</p>
                    <p class="news-date">2019-05-27 08:54</p>
                    <div class="clearfix"></div>
                </li>
                <li>
                    <p class="news-info">郑州地铁5号线国内首个实现三大运营商5G同</p>
                    <p class="news-date">2019-05-27 08:54</p>
                    <div class="clearfix"></div>
                </li>
                <li>
                    <p class="news-info">郑州地铁5号线国内首个实现三大运营商5G同</p>
                    <p class="news-date">2019-05-27 08:54</p>
                    <div class="clearfix"></div>
                </li>
            </ul>
        </div>
        <div class="wrap right">
            <div class="news-left-block"></div>
            <div class="news-title" style="border:none">
                <p>信息下载中心</p>
                <div class="clearfix"></div>
            </div>

            <ul class="news-content">
                <li>
                    <p class="news-info">能源平台说明书</p>
                    <p class="news-date">2019-05-27 14:13</p>
                    <div class="clearfix"></div>
                </li>
                <li>
                    <p class="news-info">DTU安装手册</p>
                    <p class="news-date">2019-05-27 15:41</p>
                    <div class="clearfix"></div>
                </li>
                <li>
                    <p class="news-info">电池修复利旧规程</p>
                    <p class="news-date">2019-05-27 17:08</p>
                    <div class="clearfix"></div>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {};
    },
    components: {},
    created() {},
    mounted() {},
    methods: {}
};
</script>

<style scoped lang='scss'>
.module {
    margin-top: 1.5rem;
    height: 161px;
    .wrap {
        margin-right: 1%;
        height: 100%;
        background: #fff;
        float: left;
    }
    .left {
        width: 48%;
    }
    .right {
        width: 45.1%;
    }
}

.news-left-block {
    float: left;
    width: 0.4rem;
    height: 1.1rem;
    background: #0070c9;
    margin-left: 0.7%;
    margin-right: 0.7%;
    margin-top: 12px;
}
.news-content li > .news-info:hover {
    color: #0070c9;
}
.news-content {
    padding-top: 10px;
    padding-bottom: 10px;
    li {
        height: 40px;
    }
    p {
        cursor: default;
        float: left;
        font-size: 16px;
        color: #222637;
        margin-left: 5px;
    }
    .news-info {
        float: left;
        cursor: pointer;
        width: 50%;
        margin-left: 1.8%;
        font-size: 0.9rem;
        color: #283945;
        line-height: 40px;
    }
    .news-date {
        float: right;
        cursor: pointer;
        margin-right: 1.5%;
        font-size: 14px;
        color: #acb7bf;
        line-height: 40px;
    }
}
.news-title {
    float: left;
    width: calc(100% - 0.7% - 2% - 0.7% - 0.4rem);
    margin-left: 0.7%;
    margin-right: 0.7%;
    li {
        cursor: pointer;
    }
    p {
        cursor: default;
        float: left;
        font-size: 16px;
        color: #222637;
        margin-top: 10px;
        margin-left: 5px;
        margin-bottom: 10px;
    }
    .news-info {
        float: left;
        width: 50%;
        margin-left: 1.8%;
        font-size: 0.9rem;
        color: #283945;
        line-height: 40px;
    }
    .news-content .news-date {
        float: right;
        margin-right: 1.5%;
        font-size: 14px;
        color: #acb7bf;
        line-height: 40px;
    }
}
</style>




